<script setup>
import {ref} from 'vue'
import {useAlbumStore} from '@/stores/album.js'
import {onLoad,onShow} from '@dcloudio/uni-app'
const albumStore = useAlbumStore()

onLoad(async ()=>{
  const res = await albumStore.getAlbumList()
})

const albumLength = ref(3)
const inputValue = ref("")
const choose = (index) => {
  console.log(index)
  uni.setStorageSync('selectedAlbumIndex', index);
  console.log("缓存中", uni.getStorageSync('selectedAlbumIndex'))
  // 返回上一个页面
  uni.navigateBack();
}
const show = ref(false)
function createNewAlbum() {
  show.value = true
}
function cancelNewAlbum() {
  show.value = false
}

</script>


<template>



  <view class="container">
    <view class="list" v-for="(item, index) in albumLength"
          :key="index">
      <album_title @click="choose(index)" :number="index"/>
    </view>
  </view>
  <view class="create">
    <view class="text">新建相册</view>
    <uni-icons @click="createNewAlbum" class="plus" color="#28b389" type="plus" size="60"></uni-icons>
  </view>
  <view class="name" v-show="show">
    <input class="input" v-model="inputValue" placeholder="请输入相册名"/>
    <button>确认</button>
    <button @click = "cancelNewAlbum">取消</button>
  </view>

</template>


